<script>
import { mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "widget-1",
  components: {},
  props: ["tingkat"],
  data() {
    return {
      strokeColor: "#D13647",
      chartOptions: {},
      series: [
        {
          name: "Net Profit",
          data: [30, 45, 32, 70, 40, 40, 40]
        }
      ],
      apiURL: "https://backend-siradi2.azurewebsites.net/guru",
      insight: {
        loginCounter: 1,
        infoLogCounter: 1,
        downloadLogCounter: 1,
        suspendedCounter: 44,
        suspendedInfoLogCounter: 0,
        suspendedVoidCounter: 0,
        suspendedDownloadLogCounter: 0
      }
    };
  },
  computed: {
    ...mapGetters(["layoutConfig"])
  },
  mounted() {
    this.fetchInsight();
  },
  methods: {
    fetchInsight() {
      return axios
        .get(this.apiURL + "/getInsight/" + this.tingkat)
        .then(response => {
          this.insight = response.data;
        });
    }
  }
};
</script>
